	/**
	* Obtain the auth string from the XML file in our activity and encode it.
	*/	
	prepAuth () {
    this.olidebug ("prepAuth ()");

    var authSet=APIActivity.findResourcesByType ("auth");

    if (authSet.length==0) {
      showFeedback ("Error, no authentication info found, please check your xml file.");
      return;
    }

    window.tutorgen.authString=btoa(authSet [0]);

    olidebug ("Auth string set to: " + window.tutorgen.authString);
  }

	/**
	* Obtain the tutorgen specific info from the activity XML. This sets up a specific
	* container on the server so that we can be sure the current activity only gets
	* problems served that are applicable for its context.
	*/	
	prepTutorGen () {
    this.olidebug ("prepTutorGen ()");

    var tGen=APIActivity.findResourcesByType ("tutorgen");

    this.olidebug ("Found tutorgen course specification: " + tGen);

    if (tGen.length==0) {
      this.olidebug ("Error, no tutorgen info found, please check your xml file.");
      return;
    } else {
      this.olidebug ("Inspecting " + tGen.length + " specification options ... ");
    }

    this.olidebug (JSON.stringify (tGen));

    var items=tGen [0].split (":");

    if (items.length==2) {		
      this.olidebug ("We have 2 items in our system/course/group id string");
      //this.learningSystemId = items [0];
      this.courseId = items [1];
    }  

    if (items.length==3) {	
      this.olidebug ("We have 3 items in our system/course/group id string");
      //learningSystemId = items [0];
      this.courseId = items [1];
      this.groupId  = items [2];
    }

    this.olidebug ("learningSystemId: " + this.learningSystemId + ", courseId: " + this.courseId + ", groupId: " + this.groupId);
  }

	/**
	 * content-groupings is now cgs
	 */
  createGroup () {
    this.olidebug ("createGroup ("+this.groupId+")");

    window.tutorgen.getRequestTutorgen (this.apiBaseUrl + this.learningSystemId + '/cgs/')
      .then (function (data) {
        window.tutorgen.olidebug ("Group data retrieved, analyzing ...");
        window.tutorgen.olidebug (JSON.stringify (data));

        var createRequest = {
          "ls_cg_id": window.tutorgen.groupId,
          "name" : "Custom OLI content group for course specific embedded activities",
          "course": window.tutorgen.courseId,
			    //"parent": learningSystemId
			    "parent" : null
        };

        var doCreate=true;

        if (data ["results"]) {
          var results=data ["results"];

          for (var i=0;i<results.length;i++) {
            var testGroup=results [i];

            if (testGroup ["ls_cg_id"]==window.tutorgen.groupId) {
              window.tutorgen.olidebug ("Our group Id already exists, indicating we won't have to register again ...");
              doCreate=false;
            }
          }
        }

        if (doCreate==true) {
          window.tutorgen.olidebug ("Group " + window.tutorgen.groupId + " not known to server, creating ... ");

          window.tutorgen.postRequestTutorgen (window.tutorgen.apiBaseUrl + window.tutorgen.learningSystemId + '/cgs/',createRequest)
            .then (function (result) {
              window.tutorgen.olidebug ("succeeded");
            },function (result) {
              window.tutorgen.olidebug ("failed");
            });
        }
      });
  }

	/**
	*
	*/
	checkCourse () {
   this.olidebug ("checkCourse ("+this.courseId+")");

	  // Check for a course first
	  
    window.tutorgen.getRequestTutorgen (this.apiBaseUrl + this.learningSystemId + '/courses/')
      .then (function (data) {
        window.tutorgen.olidebug ("Courses request succeeded");
        window.tutorgen.olidebug ("Course data: " + JSON.stringify (data));	

        var courses=data.results;
        var found=false;

        for (var i=0;i<courses.length;i++) {
          var testCourse=courses [i];

          window.tutorgen.olidebug ("Comparing " + testCourse.ls_course_id + " to: " + window.tutorgen.courseId);

          if (testCourse.ls_course_id==window.tutorgen.courseId) {
           window.tutorgen.olidebug ("Course already exists!");
           found=true;
          }
        }

        if (found==false) {
          getRequest ("Course not registered yet, creating ...");

          var createCourseRequest= {
            "ls_course_id" : window.tutorgen.courseId,
            "name" : "Currently not Sent"
          };

          window.tutorgen.postRequestTutorgen (window.tutorgen.apiBaseUrl + window.tutorgen.learningSystemId + '/courses/', createCourseRequest)
            .then (function (result) {
              window.tutorgen.olidebug ("succeeded");
            },function (result) {
              window.tutorgen.olidebug ("failed");
            });
        }
      },function (error){window.tutorgen.olidebug ("Error calling getRequest")});	
  }

