/**
*
*/

import $ from 'jquery';
import OLIBase from './oli-base.js';

/**
*
*/
export default class OLIExternalAdaptiveActivity extends OLIBase {

  /**
   * 
   */
  constructor (aSuperClient, aUser, aCourse, aURL, anActivity) {
	super ();
	
	this.superClient = aSuperClient;
	this.studentId = aUser;
	this.studentIdAnon = encryptUserId (aUser);
	this.apiBaseUrl = aURL;
	this.courseId = aCourse;
	this.currentProblem ="none";
	this.attemptCount=0;
	this.params = {
		'pg': 0.25,
		'ps': 0.25,
		'pi': 0.1,
		'pt': 0.5,
		'threshold': 0.99
	};
	this.skills = [
		'describesample',
		'idsampling',
		'identifybiassample',
		'critique',
		'experimvsobs',
		'surveydesign',
		'lurkingvar',
		'studydesigndefin',
		'idstudydesign'
	];
	this.aRegisterSuccessCallback=null;
	this.aRegisterFailCallback=null;
  }

  /**
  *
  */
  setCurrentProblem (aProblem) {
    currentProblem=aProblem;
  }
	
  /**
  *
  */
  getCurrentProblem () {
   return (currentProblem);
  }
	
  /**
  *
  */
  incAttemptCount () {
    attemptCount++;
  }
	
  /**
  *
  */
  setAttemptCount (aCount) {
    attemptCount=aCount;
  }
	
  /**
  *
  */
  getAttemptCount () {
    return (attemptCount);
  }
	
  /**
  * This is the generic method (interface) In the EdX version we call
  * the back-end specific call
  */
  registerUser (registerSuccessCallback,registerFailCallback) {
    this.olidebug ("registerUser ("+studentId+" -> "+studentIdAnon+ ")");
		
    this.olidebug ("Let's pretend for now that we have an anonymous id. This will ensure that for testing purposes we always get a new id");
		
    this.processGetAnonIDSucceeded ({result:'success'});
  }
	
  /**
   *
   */	
  configureUser () {
    this.olidebug ("configureUser ()");
		
    var studentConfig = {
      'course_id': courseId,
      'params': params,
      'user_id': studentIdAnon,
      'skills_list': skills
    };
		
    return $.ajax({
      url: apiBaseUrl + '/parameters/bulk',
      type: 'POST',
      contentType: 'application/json',
      data: JSON.stringify (studentConfig),
    }).done(function() {
      this.olidebug("Skills: " + skills + " is  configured");
			
	  // We don't call the success handler here since we need to continue with a 
	  // another internal function
    }).fail(function() {
      this.olidebug('Failed to configure skills: "'+skills+'" for user "'+studentIdAnon+'"');
            
      if (aRegisterFailCallback) {
        aRegisterFailCallback ();
      }	
    });		
  }
	
  /**
   *
   */	
  registerUserInEdxAdapt () {
    this.olidebug ("registerUserInEdxAdapt ()");				
        
    $.ajax({
      url: apiBaseUrl + '/course/' + courseId + '/user',
      type: 'POST',
      contentType: 'application/json',
      data: JSON.stringify({'user_id': studentIdAnon}),
    }).done(function() {
      this.olidebug("User Created");
			
	  if (aRegisterSuccessCallback) {
        aRegisterSuccessCallback ();
      }
			
      this.setStatusOk();
    }).fail(function() {
      this.olidebug("Failed to create user");
			
      this.setStatusNok();
			
	  if (aRegisterFailCallback) {
        aRegisterFailCallback ();
	  }				
    });
  }
	
  /**
   *
   */	
  pageLoadComplete () {
    this.olidebug ("pageLoadComplete ("+this.getCurrentProblem ()+")");				
        
    var pageLoad=JSON.stringify({'problem': this.getCurrentProblem ()});
		
    this.olidebug ("pageLoad: " + pageLoad);
		
    $.ajax({
      url: apiBaseUrl + '/course/' + courseId + '/user/' + studentIdAnon +'/pageload',
      type: 'POST',
      contentType: 'application/json',
      data: pageLoad,
    }).done(function() {
      this.olidebug("Pageload confirmed");			
    }).fail(function() {
      this.olidebug("Unable to confirm pageload");
    });
  }
	
	/**
	*
	*/	
    indicateProblemComplete (anEvaluation,anAttemptCount) {
		this.olidebug ("indicateProblemComplete ("+anEvaluation+")");

		var pageLoad=JSON.stringify({'problem': this.getCurrentProblem (), 'correct': anEvaluation, 'attempt': anAttemptCount});		
		        
		this.olidebug ("pageLoad: " + pageLoad);				
				
        $.ajax({
            url: apiBaseUrl + '/course/' + courseId + '/user/' + studentIdAnon +'/interaction',
            type: 'POST',
            contentType: 'application/json',
            data: pageLoad,
        })
        .done(function() {
            this.olidebug("Pageload confirmed");
			
        })
        .fail(function() {
            this.olidebug("Unable to confirm pageload");
        });
    }
		
	/**
	*
	*/	
	processGetAnonIDSucceeded (aData) {
		this.olidebug ("processGetAnonIDSucceeded ()");
				
		if (aData) {
			this.olidebug ("Data: " + JSON.stringify(aData));	
		}	
		
        this.olidebug ("Checking if student already registered in EdxAdapt ...");
		
        $.ajax({
            url: apiBaseUrl + '/course/' + courseId + '/user/' + studentIdAnon,
            type: 'GET',
            contentType: 'application/json',
        })
        .done(function(data, textStatus) {
			this.olidebug ("Success, the student already exists in Edx-Adapt: " + data + " : " + textStatus);
						
			this.setStatusOk();
			
			if (aRegisterSuccessCallback) {
				aRegisterSuccessCallback (data);
			}
        })
        .fail(function(jqXHR, textStatus) {
            if (jqXHR.status == 404) {
                this.olidebug ("Student not found in EdxAdapt. Let's configure and register them ...");
				
				$.when.apply($, this.configureUser()).then(this.registerUserInEdxAdapt());
            }
			
            this.olidebug("Failed to register student in EdxAdapt: " + jqXHR.responseText);
        });				
	};
	
	/**
	* Our anonymous id doesn't exist on the server, create it ...
	*/
	processGetAnonIDFail (aData) {
		this.olidebug ("processGetAnonIDFail ()");
		
		// store the anonymous id ...

		APIActivity.setValue ("anonymousid",studentIdAnon,this.processGetAnonIDSucceeded);
		
		// and try again ...
		
		this.processGetAnonIDSucceeded ();
	}
	
  /**
   * Get student's status data form the EdxAdapt and use it for custom event creation.
   * @param {object} data - response data from EdxAdapt
   */
	getFirstAdaptiveProblem (aCallback) {
		this.olidebug ("getFirstAdaptiveProblem ()");
		       
		$.ajax({
			url: apiBaseUrl + '/course/' + courseId + '/user/' + studentIdAnon,
			type: 'GET',
			contentType: 'application/json',
		})
		.done(function(data, textStatus) {
			this.olidebug("First problem successfully found", textStatus);   

			if (aCallback)
			{
				aCallback (data);
			}
		})
		.fail(function(jqXHR, textStatus) {
			this.olidebug("Failed to get first adaptive problem for student from EdxAdapt", jqXHR.responseText);
		});
    }
	
    /**
    * Get student's status data form the EdxAdapt and use it for custom event creation.
    * @param {object} data - response data from EdxAdapt
    */
	getNextAdaptiveProblem (aCallback)
	{
		this.olidebug ("getNextAdaptiveProblem ()");
		       
		$.ajax({
			url: apiBaseUrl + '/course/' + courseId + '/user/' + studentIdAnon,
			type: 'GET',
			contentType: 'application/json',
		})
		.done(function(data, textStatus) {
			this.olidebug("First problem successfully found", textStatus);   

			if (aCallback)
			{
				aCallback (data);
			}
		})
		.fail(function(jqXHR, textStatus) {
			this.olidebug("Failed to get first adaptive problem for student from EdxAdapt", jqXHR.responseText);
		});
    }
	
	/**
	*
	*/	
    setStatusOk () {
		this.olidebug ("setStatusOk ()");
		
        //$('#status_ok').css('visibility', 'visible');
    }
	
	/**
	*
	*/	
    setStatusNok () 
	{
		this.olidebug ("setStatusNok ()");
		
        //$('#status_nok').css('visibility', 'visible');
    }
	
    /**
    * Create and publish custom event with url to the problem chosen by EdxAdapt.
    * @param {object} data - response data from EdxAdapt
    */
    createEnrollOkEvent (data) {
        var problemUrl;
		
        if (data.current) {
            problemUrl = data.current.tutor_url;
        }
		else if (data.next) {
            problemUrl = data.next.tutor_url;
        }
        if (problemUrl) {
            var event = new CustomEvent("edxAdaptEnrollOk", {detail: { adaptiveProblem: problemUrl } });
            document.dispatchEvent(event);
        }
		else {
            this.olidebug ("Failed to get tutor_url to the first adaptive problem");
        }
    }
}
