import OLIBase from './oli-base.js';

var rocketDriver=null;

/**
* References:
*
*  Authentication: https://rocket.chat/docs/developer-guides/iframe-integration/authentication/
*  Commands: https://rocket.chat/docs/developer-guides/iframe-integration/commands/
*   
*/  
export default class OLIRocketChat extends OLIBase { 
  
  /**
   *
   */   
  constructor () {
    super ();

    this.olidebug ("OLIRocketChat ()");

    // Calling this will remove any extraneous margins, padding and borders of embed.html
    APIActivity.maximizeClientArea ();

    // Make sure we have a findable name in the logfiles
    APIActivity.setActivityName ("RocketChat");

    // Allow the activity to be popped out, moved and resized witin the window
    // APIActivity.makePopout();    

    rocketDriver=this;

    window.driverPointer=this;

    // We're assuming that RocketChat is installed on the same system as the activity is served
    // from. You will need a proxy to ensure the correct back-end code is mapped.
    this.adminURL = "/rocketchat";
    this.chatServer = APIActivity.findResourcesByType ("server");
    this.rocketURL = this.chatServer+"/api/v1/";
    this.rocketId = "";

    // RocketChat does not allow - or . delimiters so we're forced to just contatenate
    // We'll need to check if this method works with some unsual user names allowable
    // by OLI
    this.oliUser = "oli"+APIActivity.getUserId ();
    this.channel = APIActivity.findResourcesByType ("channel");
    this.group = APIActivity.findResourcesByType ("group");
    this.course = "DEFAULT";

    this.iframe = null;
    this.authToken = ""; // Admin Token
    this.authID = ""; // Admin ID
    this.loginData = {};

    this.userID = ""; // The ID of the user to be logged in
    this.userToken = ""; // The Token of the user to be logged in 
    this.genPass = "";

    this.userAuthToken=""; // User token
    this.userAuthId=""; // User ID

    if (window.parent.workbookConfig) {
      rocketDriver.group=window.parent.workbookConfig.userGroup;
      rocketDriver.course=window.parent.workbookConfig.courseKey;
      rocketDriver.room = rocketDriver.course + "-" + rocketDriver.group + "-" + rocketDriver.channel;
    }    

    // We're making use of the fact that we can have the browser load the RocketChat website and
    // configure the user in the back-end at the same time.
    this.insertRocketChatPage ();

    var data={
      "name" : rocketDriver.getFullname (),
      "user" : rocketDriver.oliUser,
      "group" : rocketDriver.group,
      "room" : rocketDriver.room
    };

    // Ask the server to take on all the tasks of creating a user entry in both its own database
    // and in Rocket chat if it doesn't exist and also setup the group channel if it doesn't
    // exist. The result of this call should be a JSON object with enough information so that
    // this code can login as the user and manage the chat window
    rocketDriver.postRequest (rocketDriver.adminURL + "/" + "configureuser",
                              data,
                              rocketDriver.configureSucceed,
                              rocketDriver.configureFailed); 
  }

  /**
  *
  */
  configureSucceed (data) {
    rocketDriver.olidebug ("configureSucceed ()");       
    rocketDriver.userID = data.u;
    rocketDriver.genPass = data.p;
    rocketDriver.loginUser ();
  }
 
  /**
  *
  */
  configureFailed () {
    rocketDriver.olidebug ("configureFailed ()");
    rocketDriver.showError ("Error configuring OLI user with RocketChat");
  }

  /**
  *
  */
  getFullname () {
    rocketDriver.olidebug ("getFullname ()");

    var fullName=rocketDriver.oliUser;

    var cfg=APIActivity.getSessionData ();

    if (cfg!=null) {
      var tmpName=$(cfg).find("metadata").find("user").attr('first_name') + " " + $(cfg).find("metadata").find("user").attr('last_name');
      if (tmpName) {
        rocketDriver.olidebug ("Generated full name: " + tmpName);
        fullName=tmpName;
      }
    }

    return (fullName);
  }
  
  /**
  *
  */
  loginUser () {
    rocketDriver.olidebug ("loginUserContinue ()");

    var data = {
      "user": rocketDriver.userID, 
      "password": rocketDriver.genPass
    };

    rocketDriver.postRequest (rocketDriver.rocketURL+"login",
                              data,
                              rocketDriver.userLoginSucceed,
                              rocketDriver.userLoginFailed);
  }

  /**
  * Login succeeded, now make sure we store the auth token! We should be able to
  * log the user into the iframe.
  */
  userLoginSucceed (e) {
    rocketDriver.olidebug ("userLoginSucceed ()");
    rocketDriver.olidebug (JSON.stringify (e));

    rocketDriver.loginData=e; // Make sure we have a copy of the full data
    rocketDriver.userAuthToken=e.data.authToken; // This is the vital link. This token will be used from now on
    rocketDriver.userAuthId=e.data.userId; // This is the vital link. This token will be used from now on

    rocketDriver.rocketId=e.data.userId;
    rocketDriver.olidebug ("Setting (rocketchat) user id to: " + rocketDriver.rocketId);
    APIActivity.setValue ("rocketId",rocketDriver.rocketId);

    rocketDriver.insertRocketChatPage ();
  }

  /**
  *
  */
  userLoginFailed (e) {
    rocketDriver.olidebug ("userLoginFailed ()");
    rocketDriver.showError ("Error: unable to log into: " + rocketDriver.rocketURL);
  }

  /**
  * Create an iframe and point it to the appropriate channel:
  *
  * https://eberly-demo.rocket.chat/channel/general
  */
  insertRocketChatPage () {
    rocketDriver.olidebug ("insertRocketChatPage ("+rocketDriver.chatServer+")");

    //rocketDriver.olidebug ("Using group id: " + rocketDriver.room + " to identify this chat");

    $("#rocketframe").attr('src', rocketDriver.chatServer+"?layout=embedded");

    window.addEventListener('message', rocketDriver.processMessageEvent,true);
    rocketDriver.iframe=document.getElementById ("rocketframe");
    if (rocketDriver.iframe!=null) {
      rocketDriver.iframe.addEventListener('load',rocketDriver.postLogin,true);
      rocketDriver.iframe.addEventListener('error',rocketDriver.rocketError,true);
    } else {
      rocketDriver.olidebug ("Internal error: can't obtain pointer to rocketchat iframe");
    }
  }

  /**
    Use this call to post a message to #general:

    Test via: https://rocket.chat/docs/developer-guides/rest-api/chat/postmessage/
    
    curl -H "X-Auth-Token: 9HqLlyZOugoStsXCUfD_0YdwnNnunAJF8V47U3QHXSq" \
         -H "X-User-Id: aobEdbYhXfu5hkeqG" \
         -H "Content-type:application/json" \
          http://localhost:3000/api/v1/chat.postMessage \
         -d '{ "channel": "#general", "text": "This is a test!" }'
   */
  postMessage (aMessage,aSucceedCallback,aFailCallback) {
    rocketDriver.olidebug ("postMessage ("+aMessage+")");

    var data = {
      "channel" : "#general",
      "text" :  aMessage
    }

    rocketDriver.postRequest (rocketDriver.rocketURL + "chat.postMessage",
      data,
      aSucceedCallback,
      aFailCallback, { 
       "X-Auth-Token": rocketDriver.authToken, 
       "X-User-Id" : rocketDriver.authID
    });
  }

  /**
  * Via: https://rocket.chat/docs/developer-guides/rest-api/chat/postmessage/#example-call
  */
  postInChannel (aChannel) {
    rocketDriver.olidebug ("postInChannel ()");

    var data = { 
      "channel": "#"+aChannel, 
      "text": (rocketDriver.getFullname () + " joined this channel") 
    };

    rocketDriver.postRequest (rocketDriver.rocketURL + "chat.postMessage",
      data,
      function () {},
      function () {}, { 
       "X-Auth-Token": rocketDriver.userAuthToken, 
       "X-User-Id" : rocketDriver.userAuthId
    });    
  }

  /**
  * Via: https://rocket.chat/docs/developer-guides/iframe-integration/commands/
  */
  goToChannel (aChannel) {
    rocketDriver.olidebug ("goToChannel ()");

    rocketDriver.iframe.contentWindow.postMessage({
      externalCommand: 'go',
      path: ('/channel/'+aChannel+"?layout=embedded")
    }, '*')
  }

  /**
  * Via: https://rocket.chat/docs/developer-guides/iframe-integration/commands/
  * and: https://github.com/RocketChat/iframe-auth-example
  */
  postLogin () {
    rocketDriver.olidebug ("postLogin ()");
    //rocketDriver.olidebug ("Waiting one or more seconds for the page to quiet down ...");

    setTimeout(function() { 
      rocketDriver.olidebug ("Logging in ...");
      //rocketDriver.olidebug ("oAuth token: " + rocketDriver.userAuthToken + " + URL: " + rocketDriver.chatServer);

      var message = {
        externalCommand: 'login-with-token',
        token: (rocketDriver.userAuthToken)
      };

      rocketDriver.olidebug (JSON.stringify (message));

      rocketDriver.iframe.contentWindow.postMessage(message, '*');

      rocketDriver.goToChannel (rocketDriver.room);

      rocketDriver.postInChannel (rocketDriver.room);
    }, 1000);
  }

  /*
  * notification                    - Fired when a user receives a notification
  * unread-changed-by-subscription  - Fired each time a user’s subscription record changes (i.e.: unread counts, etc)
  * unread-changed                  - Fired when the pages title changes
  * room-opened                     - Fired when a room is opened
  * new-message                     - Fired everytime the opened room receives a new message
  * click-user-card-message         - Fired when the user clicks on a username link
  * click-mention-link              - Fired when the user clicks on a mention link
  * click-message-link              - Fired when the user clicks on a posted link
  * click-action-link               - Fired when the user clicks on an action link button (i.e.: “click to join” to video conferences)
  */
  processMessageEvent (e) {
    rocketDriver.olidebug ("processMessageEvent ()");
    rocketDriver.olidebug (e.data.eventName); // event name
    rocketDriver.olidebug (e.data.data); // event data

    if (e.data.eventName=="notification") {
      rocketDriver.olidebug ("notification");
      APIActivity.logNavigation ("rocketDriver.room","notification");
    }

    if (e.data.eventName=="unread-changed-by-subscription") {
      rocketDriver.olidebug ("unread-changed-by-subscription");
      APIActivity.logNavigation ("rocketDriver.room","notification");
    }

    if (e.data.eventName=="unread-changed") {
      rocketDriver.olidebug ("unread-changed");
      APIActivity.logNavigation ("rocketDriver.room","unread-changed");
    }
    
    if (e.data.eventName=="room-opened") {
      APIActivity.logNavigation ("rocketDriver.room","room-opened");
      rocketDriver.olidebug ("Chat ready, hiding scrim ...");
      $("#dbgoutput").css('visibility', 'hidden');
    }

    if (e.data.eventName=="new-message") {
      rocketDriver.olidebug ("new-message");
      APIActivity.logNavigation ("rocketDriver.room","new-message");
    }
    
    if (e.data.eventName=="click-user-card-message") {
      rocketDriver.olidebug ("click-user-card-message");
      APIActivity.logNavigation ("rocketDriver.room","click-user-card-message");
    }
 
    if (e.data.eventName=="click-mention-link") {
      rocketDriver.olidebug ("click-mention-link");
      APIActivity.logNavigation ("rocketDriver.room","click-mention-link");
    }

    if (e.data.eventName=="click-message-link") {
      rocketDriver.olidebug ("click-message-link");
      APIActivity.logNavigation ("rocketDriver.room","click-message-link");
    }

    if (e.data.eventName=="click-action-link") {
      rocketDriver.olidebug ("click-action-link");
      APIActivity.logNavigation ("rocketDriver.room","click-action-link");
    }     
  }

  /**
  *
  */
  showError (aMessage) {
    rocketDriver.olidebug ("showError ("+aMessage+")");
    if ($("#dbgconsole")) {
      $("#dbgconsole").text (aMessage);
    }
  }

  /**
  *
  */
  showMessage (aMessage) {
    rocketDriver.olidebug ("showMessage ("+aMessage+")");
    if ($("#dbgconsole")) {
      $("#dbgconsole").text (aMessage);
    }
  }  

  /**
  *
  */
  rocketError (err) {
    rocketDriver.olidebug ("rocketError ()");
    rocketDriver.olidebug (err);
  }
}

