  /**
   *
   */
  getDownloadableResource(aResource, aPath) {
    // this.olidebug ("getDownloadableResource ("+aResource+","+aPath+")");

    if (aPath == null) {
      aPath = "";
      }

    var translated = this.superClient.webContentFolder + aPath + aResource;

    if ((aResource.indexOf("http://") != -1) ||
        (aResource.indexOf("https://") != -1)) {
      translated = aResource;
      }

    // this.olidebug ("Created resource: " + translated);

    return (translated);
  }

