  logNavigation (qId,aRoute) {
    APIActivity.olidebug ("logNavigation ("+qId+","+aRoute+")");
    var action = new ActionLog(CommonLogActionNames.START_STEP,
      APIActivity.superClient.sessionId,
      APIActivity.superClient.resourceId,
      APIActivity.superClient.activityGuid,
      APIActivity.activityName,
      APIActivity.superClient.timeZone);
    // Important: the usage of START_STEP_TRACK allows dashboard tracking
    var supplement = new SupplementLog();
    supplement.setAction(CommonLogActionNames.START_STEP_TRACK);
    supplement.setSource(qId);
    supplement.setInfoType("navigation");
    supplement.setInfo(aRoute);
    action.addSupplement(supplement);
    APIActivity.superClient.logAction(action);
  }

