/**
 *
 */

import $ from 'jquery';

import OLIBase from './oli-base.js';

/**
* Container for a lot of convenience functions that should be removed from OLIAPI
*/	
export default class OLIBaseDriver extends OLIBase {	
	
  /**
   *
   */		
  constructor () {
	  super ();

    this.olidebug ("OLIBaseDriver ()");
  }

  /**
   *
   */
  getWindowHeight () {
    var body = document.body;
    var html = document.documentElement;

    var height = Math.max (body.scrollHeight,
                           body.offsetHeight,
                           body.clientHeight,            
                           html.clientHeight,
                           html.scrollHeight,
                           html.offsetHeight);            
    return (height);
  }

  /**
   *
   */
  getDocumentHeight () {
    return (document.body.clientHeight);
  } 

  /**
   *
   */
  makeid() {
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

    for (var i = 0; i < 5; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }

    return text;
  }

  /**
   * Shuffles array in place.
   * @param {Array} a items The array containing the items.
   */
  shuffle(a) { 
    var j, x, i;
  
    for (i = a.length; i; i--) {
      j = Math.floor(Math.random() * i);
      x = a[i - 1];
      a[i - 1] = a[j];
      a[j] = x;
    }
  
    return (a);
  }  

  /**
   * Convenience method to get an object that has the location of a div
   */
  getPos(el) {
    return ({
      x: el.offsetLeft,
      y: el.offsetTop
    });
  }  

  /**
  * Convenience function to add an event to a div, etc. It makes sure the
  * proper API is used depending on the browser environment.
  */
  addEvent(obj, evt, fn) {
    if (obj.addEventListener) {
      obj.addEventListener(evt, fn, true);
    }
    else if (obj.attachEvent) {
      obj.attachEvent("on" + evt, fn);
    }
  }  

  /**
   * We will provbably remove this soon.
   */
  JQueryAltGetScript(source, callback) {
    var script = document.createElement('script');
    var prior = document.getElementsByTagName('script')[0];
    script.async = 1;

    script.onload = script.onreadystatechange = function( _, isAbort ) {
      if(isAbort || !script.readyState || /loaded|complete/.test(script.readyState) ) {
        script.onload = script.onreadystatechange = null;
        script = undefined;

        if(!isAbort) { if(callback) callback(); }
      }
    };

    script.src = source;

    prior.parentNode.insertBefore(script, prior);
  }    
}
