/**
* This file determines what classes end up in the library. Anything that is exported
* below will be included. It also forms the single point where the client boots.
*
* https://stackoverflow.com/questions/37656592/define-global-variable-with-webpack
*/

console.log ("Parsing OLI driver ...");

// Let's use this form of creating global variables. Seems to be the
// safest route in a webpack environment. Right now we're going with
// the idea that there is one known global variable that provides access
// to all the other data.
window.APIActivity = null;

import OLIBase from './oli-base.js';
//import OLILogWrapper from './oli-logwrapper.js';
import OLIExternalAdaptiveActivity from './oli-adapt.js';
import OLICTATDriver from './oli-ctatdriver.js';
import OLIAPI from './oli-api.js';
import OLIDashboard from './oli-dashboard.js';
import OLILTI from './oli-lti.js';
import OLITutorgenAdaptiveActivity from './oli-tutorgen.js';

// Make selected classes available outside the library
export { OLIBase, OLIExternalAdaptiveActivity, OLICTATDriver, OLIAPI, OLIDashboard, OLILTI, OLITutorgenAdaptiveActivity };

const APIActivity = new OLIAPI();
window.APIActivity = APIActivity;
export default APIActivity;
/**
 * Initialization function called from
 * /oli/superactivity/embed/olisuperactivity.js
 * @param super_client:SuperActivityClient
 * @param data:xml
 */
export function init(super_client, data) {
  return APIActivity.init(super_client, data);
}

/**
 * This is unfortunately necessary right now since the embed.html doesn't know if, when and
 * how to add the module tag to the script tag. At least that's my understanding how it
 * would be possible to directly load classes.
 */
window.tutorgenBuilderFactory = function (aSuperClient,aUser,aCourse,aURL,anActivity) {
  return (new OLITutorgenAdaptiveActivity (aSuperClient,aUser,aCourse,aURL,anActivity));
}

console.log ("Library parsed, we should now be booting");

// Fix for code in /oli/superactivity/embed/olilogging.js:
//  .context is deprecated and has been removed in jQuery.
window.oliLogger.logAction = function (logServiceUrl, actionLog, callback) {
  if (!(actionLog instanceof ActionLog)) {
    throw "Invalid type = actionLog not instanceof ActionLog";
  }
  var logData = (new XMLSerializer()).serializeToString(actionLog.toXML()[0]);
  $.post(logServiceUrl, logData).done(callback);
};
