import OLIBase from './oli-base.js';
import CTATXML from './xml.js';

/**
 *
 */
export default class OLICTATDriver extends OLIBase {

  /**
   *
   */
  constructor(brdName) {
    super();

    // this.firstAttempt=false;
    this.tutorDone = false;

    this.olidebug("OLICTATDriver ()");

    this.questionName = brdName || "undefined";
  }

  /**
   *
   */
  getTutorDone() { return (this.tutorDone); }

  setTutorDone(b) { this.tutorDone = b; }

  /**
   *
   */
  logActivityStart(aQuestion) {
    this.olidebug("logActivityStart ()");

    this.questionName = aQuestion;

    var action = new ActionLog(
        CommonLogActionNames.START_STEP, APIActivity.getSuperClient().sessionId,
        APIActivity.getSuperClient().resourceId,
        APIActivity.getSuperClient().activityGuid, "CTAT_TUTOR",
        APIActivity.getSuperClient().timeZone);
    var supplement = new SupplementLog();
    supplement.setAction(CommonLogActionNames.START_STEP_TRACK);
    supplement.setSource(this.questionName);
    supplement.setInfoType("CTAT_SESSIONSTART");

    // supplement.setInfo (JSON.stringify (evt));

    action.addSupplement(supplement);

    APIActivity.getSuperClient().logAction(action);
  }

  /**
   * Annoying: 'this' is not valid since we're called from an
   * external library's 'this'
   */
  processProblemSummary(problemString, SCORMString) {
    APIActivity.olidebug("processProblemSummary ()");

    console.log(problemString);

    var summaryParser = new CTATXML();

    var summaryRoot = summaryParser.parse(problemString);

    var uniqueString = summaryParser.getElementAttr(summaryRoot, "UniqueSteps");
    var totalString = summaryParser.getElementAttr(summaryRoot, "RequiredSteps");

    var unique = parseInt(uniqueString);
    var total = (parseInt(totalString) || 0);

    if (total <= 0) {
      APIActivity.olidebug(
          "We're just getting started, the tracer can't determine how many total steps there are.");
    } else {
      var percentage = unique / total;

      APIActivity.olidebug("Percentage: " + percentage + ", unique: " + unique + ", total: " + total);

      APIActivity.getSuperClient().scoreAttempt(
          'percent', percentage, function(response) {
            APIActivity.olidebug("scoreAttempt() server response " + response);

            if (APIActivity.getCTATDriver().getTutorDone() == true) {
              //APIActivity.getSuperClient().endAttempt(function(
              //    endAttemptResponse) {
              //  APIActivity.olidebug(
              //      "scoreAttempt() endAttempt server response" +
              //      endAttemptResponse);
              //});
              APIActivity.olidebug("scoreAttempt() Not calling endAttempt to preserve visibility");
            }
          });
    }
  }

  /**
   *
   */
  logCorrect(sai, msg) {
    this.olidebug("logCorrect () sai " + sai + ", msg " + (msg && msg.getXMLString ? msg.getXMLString() : msg) +
                  ", msg.isLoggingSuppressed() " +(msg && typeof(msg.isLoggingSuppressed) == "function"
                       ? msg.isLoggingSuppressed()
                       : null));

    /**
     * @return true if should log; so far, needed only on logCorrect since
     * checks only problem restore, TPAs
     */
    var shouldLog = function(m) {
      if (!m)
        return false;
      if (typeof(m.isLoggingSuppressed) == "function" &&
          m.isLoggingSuppressed())
        return false; // logging suppressed while restoring state, e.g.
      var actor = m.getProperty("Actor");
      return (!actor) || (/^ *student/i.test(
                             actor)); // log if no actor or actor is "student*"
    };

    // CTATCommShell.commShell.sendProblemSummaryRequest(this.processProblemSummary);
    // let CTATLMS.gradeStudent do it

    if (!shouldLog(msg))
      return;

    // Then log what just happened to the log service

    var action =
        new ActionLog(CommonLogActionNames.SUBMIT_ATTEMPT,
                      APIActivity.getSuperClient().sessionId,
                      APIActivity.getSuperClient().resourceId,
                      APIActivity.getSuperClient().activityGuid, "CTAT_TUTOR",
                      APIActivity.getSuperClient().timeZone);

    var supplement = new SupplementLog();
    supplement.setAction(CommonLogActionNames.EVALUATE_QUESTION);
    supplement.setSource(this.questionName);
    var stepID = (msg ? String(msg.getProperty("StepID")).trim() : "");
    var selection = (sai ? sai.getSelection() : "");
    var act =  (sai ? sai.getAction() : "");
    var infoType = (stepID || (selection && act ? selection+" "+act : (selection.toLowerCase() == "done" ? "Done" : "_noNamedComponent_")));

    this.olidebug("logCorrect () supplement sai "+sai+", selection "+selection+" "+typeof(selection)+", infoType "+infoType+", stepID " + stepID);

    supplement.setInfoType(infoType); // (stepID || selection || "_noNamedComponent_");
    supplement.setInfo(sai && typeof(sai.toString) == "function" ? sai.toString() : sai);

    // supplement.setInfo (JSON.stringify (msg));

    action.addSupplement(supplement);

    // Use logging to connect to the Dashboard

    var trackSupplement = new SupplementLog();
    trackSupplement.setAction(CommonLogActionNames.EVALUATE_RESPONSE_TRACK);
    trackSupplement.setSource(this.questionName);
    trackSupplement.setInfoType(infoType);
    trackSupplement.setInfo("true");
    action.addSupplement(trackSupplement);

    // Log the action ...

    APIActivity.getSuperClient().logAction(action);

    /*
      this service call to endAttempt() should be moved:
      * for single activities it should perhaps be a .then() call on a promise
      for the final scoreAttempt
      * for sequences it should be invoked when the sequencer sees no more
      problems to run

      if (this.stringCompare(selection, "done") == true) {  // on correct done
      step, end OLI attempt
      this.tutorDone = true;
      APIActivity.getSuperClient().endAttempt(function (endAttemptResponse) {
      APIActivity.olidebug("scoreAttempt() endAttempt server response" +
      endAttemptResponse);
      });
      }
    */
  }

  /**
   *
   */
  logIncorrect(sai, msg) {
    this.olidebug("logIncorrect ()");

    // Then log what just happened to the log service

    var action =
        new ActionLog(CommonLogActionNames.SUBMIT_ATTEMPT,
                      APIActivity.getSuperClient().sessionId,
                      APIActivity.getSuperClient().resourceId,
                      APIActivity.getSuperClient().activityGuid, "CTAT_TUTOR",
                      APIActivity.getSuperClient().timeZone);

    var supplement = new SupplementLog();
    supplement.setAction(CommonLogActionNames.EVALUATE_QUESTION);
    supplement.setSource(this.questionName);
    var stepID = (msg ? String(msg.getProperty("StepID")).trim() : "");
    var selection = (sai ? sai.getSelection() : "");
    var act =  (sai ? sai.getAction() : "");
    var infoType = (stepID || (selection && act ? selection+" "+act : (selection.toLowerCase() == "done" ? "Done" : "_noNamedComponent_")));

    this.olidebug("logIncorrect () supplement sai "+sai+", selection "+selection+" "+typeof(selection)+", infoType "+infoType+", stepID " + stepID);

    supplement.setInfoType(infoType); // (stepID || selection || "_noNamedComponent_");
    supplement.setInfo(sai && typeof(sai.toString) == "function" ? sai.toString() : sai);

    // supplement.setInfo (JSON.stringify (msg));

    action.addSupplement(supplement);

    // Use logging to connect to the Dashboard

    var trackSupplement = new SupplementLog();
    trackSupplement.setAction(CommonLogActionNames.EVALUATE_RESPONSE_TRACK);
    trackSupplement.setSource(this.questionName);
    trackSupplement.setInfoType(infoType);
    trackSupplement.setInfo("false");
    action.addSupplement(trackSupplement);

    // Log the action ...

    APIActivity.getSuperClient().logAction(action);
  }

  /**
   *
   */
  logHint(sai, msg) {
    this.olidebug("logHint ()");

    var action = new ActionLog(
        CommonLogActionNames.VIEW_HINT, APIActivity.getSuperClient().sessionId,
        APIActivity.getSuperClient().resourceId,
        APIActivity.getSuperClient().activityGuid, "CTAT_TUTOR",
        APIActivity.getSuperClient().timeZone);

    var supplement = new SupplementLog();
    supplement.setAction(CommonLogActionNames.VIEW_HINT);
    supplement.setSource(this.questionName);
    var stepID = (msg ? String(msg.getProperty("StepID")).trim() : "");
    var selection = (sai ? sai.getSelection() : "");
    var act =  (sai ? sai.getAction() : "");
    var infoType = (stepID || (selection && act ? selection+" "+act : (selection.toLowerCase() == "done" ? "Done" : "_noNamedComponent_")));

    this.olidebug("logCorrect () supplement sai "+sai+", selection "+selection+" "+typeof(selection)+", infoType "+infoType+", stepID " + stepID);

    supplement.setInfoType(infoType); // (stepID || selection || "_noNamedComponent_");
    supplement.setInfo(msg.getProperty("TutorAdvice"));
    action.addSupplement(supplement);

    // Important: allows dashboard tracking

    var trackSupplement = new SupplementLog();
    trackSupplement.setAction(CommonLogActionNames.VIEW_HINT_TRACK);
    trackSupplement.setSource(this.questionName);
    trackSupplement.setInfoType(infoType);
    trackSupplement.setInfo(msg.getProperty("TutorAdvice"));
    action.addSupplement(trackSupplement);

    APIActivity.getSuperClient().logAction(action);
  }

  /**
   *
   */
  enableCTATDashboardFunctionality(brdName, tutorWindow) {
    this.olidebug("enableCTATDashboardFunctionality () brdName " + brdName +
                  ", tutorWindow " + tutorWindow);

    var assocRulesListener = {
      processCommShellEvent : function(evt, msg, actor) {
        // Remember that we do not have access to 'this' so we'll have to get a
        // new pointer
        // to the CTAT driver
        var CTATDriver = APIActivity.getCTATDriver();

        if (evt == "AssociatedRules") {
          CTATDriver.olidebug("processCommShellEvent (AssociatedRules)");

          var indicator = msg.getIndicator();
          var sai = msg.getSAI(); // selection-action-input from tutor engine

          CTATDriver.olidebug("Indicator: " + indicator);

          if ("incorrect" == indicator.toLowerCase()) {
            CTATDriver.olidebug("Tutor's answer is " + sai.toString());
            CTATDriver.logIncorrect(sai, msg);
            }

          if ("correct" == indicator.toLowerCase()) {
            CTATDriver.olidebug("Tutor's answer is " + sai.toString());
            APIActivity.getCTATDriver().setTutorDone((sai.getSelection()||"sel").toLowerCase() == "done");
            CTATDriver.logCorrect(sai, msg);
            }

          if ("hint" == indicator.toLowerCase()) {
            CTATDriver.olidebug("Tutor's answer is " + sai.toString());
            CTATDriver.logHint(sai, msg);
          }
        }
      }
    };

    if (tutorWindow)
      tutorWindow.CTATCommShell.commShell.addGlobalEventListener(
          assocRulesListener);
    else
      CTATCommShell.commShell.addGlobalEventListener(assocRulesListener);

    /*
    // startAttempt is something you want to use if an activity resets
    // for example. The gradebook will close an existing attempt at
    // whatever percentage it was done at and start a new one.

    APIActivity.getSuperClient ().startAttempt ();
    */

    //this.logActivityStart(brdName);
  }

  setCTATLMS() {
    window.CTATTarget = 'OLI';
    window.CTATLMS.identifier = 'OLI';
    let brd = this.questionName;
    window.CTATLMS.initLMSConnection = function() {
      let sc = window.APIActivity.getSuperClient();
      var action = new ActionLog(
        CommonLogActionNames.START_STEP, sc.sessionId, sc.resourceId,
        sc.activityGuid, "CTAT_TUTOR", sc.timeZone);
      var supplement = new SupplementLog();
      supplement.setAction(CommonLogActionNames.START_STEP_TRACK);
      supplement.setSource(brd);
      supplement.setInfoType("CTAT_SESSIONSTART");
      action.addSupplement(supplement);
      sc.logAction(action);
    };
    window.CTATLMS.closeLMSConnection = function() {
      let sc = window.APIActivity.getSuperClient();
      APIActivity.olidebug("OLICTATDriver CTATLMS.closeLMSonnection() no-op");
      // sc.endAttempt(function(endAttemptResponse) {
      //   APIActivity.olidebug("scoreAttempt() endAttempt server response" +
      //                        endAttemptResponse);
      // });
    };
    window.CTATLMS.saveProblemState = function(state) {
      let sc = window.APIActivity.getSuperClient();
      sc.writeFileRecord(brd, 'application/json', sc.currentAttempt,
                         state.problem_state);
    };
    window.CTATLMS.getProblemState = function(handler) {
      let sc = window.APIActivity.getSuperClient();
      sc.loadFileRecord(brd, sc.currentAttempt, function(data) {
        // CTAT now emits JSON but wants a string.
        handler(JSON.stringify(data));
      });
    };
    window.CTATLMS.gradeStudent = function(correct_steps, required_steps) {
      APIActivity.olidebug("OLICTATDriver CTATLMS.gradeStudent(correct_steps "+correct_steps+", required_steps "+required_steps+")");
      let sc = window.APIActivity.getSuperClient();
      let completed = false;
      let score = 0.0;
      if (required_steps <= 0) {
        score = 1.0;
        completed = true;
      } else {
        score = (correct_steps / required_steps);
        completed = correct_steps >= required_steps;
      }
      sc.scoreAttempt('percent', score);
    };
  }
}
